import React, { useRef, useState, useEffect } from 'react';
import VisibilitySensor from 'react-visibility-sensor';

function AudioVisualizer(props) {
    const [audioElement, setAudioElement] = useState(null);
    const [analyser, setAnalyser] = useState(null);
    const [audioCtx, setAudioCtx] = useState(null);
    const [source, setSource] = useState(null);
    const [loaded, setLoaded] = useState(false);
    const canvasAreaRef = useRef(null);
    const fileInputRef = useRef(null);
    const frequencyRef = useRef(null);
    const backgroundRef = useRef(null);
    const scrubBarRef = useRef(null);
    const volumeBarRef = useRef(null);
    const [play, setPlay] = useState(false);
    const [mouseOverBottom1, setMouseOverBottom1] = useState(false);
    const [mouseOverBottom2, setMouseOverBottom2] = useState(false);
    const [mouseOverBottom3, setMouseOverBottom3] = useState(false);
    const [isVisible, setIsVisible] = useState(true);

    useEffect(() => {
        if (analyser && source && audioCtx) {
            source.connect(analyser);
            analyser.connect(audioCtx.destination);
            togglePlay(true);

            const bufferLength = analyser.frequencyBinCount;
            let dataArray = new Uint8Array(bufferLength);

            const frequencyCtx = frequencyRef.current.getContext("2d");
            const smudgeCtx = backgroundRef.current.getContext("2d", { willReadFrequently: true });
            const hiddenCanvas = document.createElement("CANVAS")
            const hiddenCtx =  hiddenCanvas.getContext("2d");
            backgroundRef.current.style.width ='100%';
            backgroundRef.current.style.height='100%';
            frequencyRef.current.style.width ='100%';
            frequencyRef.current.style.height='100%';
            backgroundRef.current.width  = canvasAreaRef.current.offsetWidth;
            backgroundRef.current.height = canvasAreaRef.current.offsetHeight;
            backgroundRef.current.style.transition = "all 2s";
            backgroundRef.current.style.opacity = "0";
            frequencyRef.current.width  = canvasAreaRef.current.offsetWidth;
            frequencyRef.current.height = canvasAreaRef.current.offsetHeight;
            hiddenCanvas.width = canvasAreaRef.current.offsetWidth;
            hiddenCanvas.height = canvasAreaRef.current.offsetHeight;

            canvasAreaRef.current.addEventListener("resize", () => {
                backgroundRef.current.width  = canvasAreaRef.current.offsetWidth;
                backgroundRef.current.height = canvasAreaRef.current.offsetHeight;
                frequencyRef.current.width  = canvasAreaRef.current.offsetWidth;
                frequencyRef.current.height = canvasAreaRef.current.offsetHeight;
                backgroundRef.current.style.transition = "all 2s";
                hiddenCanvas.width = canvasAreaRef.current.offsetWidth;
                hiddenCanvas.height = canvasAreaRef.current.offsetHeight;
            });

            let ultimatePeak = 1;
            let rotationAngle = 0;
            let rotateSmudge = false;
            let splitSmudge = true;
            let growSmudge = false;
            let smudgeVelocity = 0;
            let smudgeGrowVelocity = 0;
            var imageData;
            let doubles = 1, doublesGoal = 1;

            // Draw the frequency spectrum visualization
            function drawVisualization() {
                if (audioElement.src.charAt(audioElement.src.length - 1) === "!") return;
                requestAnimationFrame(drawVisualization);  
                analyser.getByteFrequencyData(dataArray);
                
                let avgAmplitude = 0;
                let highFreqAvgAmplitude = 0;
                let highFreqPeakAmplitude = 0;
                let midFreqAvgAmplitude = 0;
                let lowFreqAvgAmplitude = 0;
                let lowFreqPeakAmplitude = 0;
                if (dataArray && dataArray.length > 0) {
                    let gradient = smudgeCtx.createLinearGradient(0, 0, smudgeCtx.canvas.width, 0);
                    let highFrequencyColor = getHighFrequencyColor(dataArray);
                    gradient.addColorStop(0, highFrequencyColor);
                    gradient.addColorStop(0.5, getLowFrequencyColor(dataArray));
                    gradient.addColorStop(1, highFrequencyColor);
                    smudgeCtx.fillStyle = gradient;
                    let verticalCenter = smudgeCtx.canvas.height / 2;
                    let zeroAdjust = 0;
                    for (let i = dataArray.length - 1; i >= 0; i--) {
                        if (dataArray[i] - 20 > 0) break;
                        zeroAdjust++;
                    }
                    let barWidth = ((smudgeCtx.canvas.width / dataArray.length)) / 2;
                    let x = 0;
                    let x2 = smudgeCtx.canvas.width;
                    for (let i = 0; i < dataArray.length - zeroAdjust; i++) {
                        drawVisualizationBar(i, barWidth, x, verticalCenter);
                        drawVisualizationBar(i, barWidth, x2, verticalCenter);
                        x += (smudgeCtx.canvas.width / (dataArray.length - zeroAdjust)) / 2;
                        x2 -= (smudgeCtx.canvas.width / (dataArray.length - zeroAdjust)) / 2;
                    }
                    avgAmplitude = getAverageAmplitude(dataArray, 0, dataArray.length);
                    highFreqAvgAmplitude = getAverageAmplitude(dataArray, 12 * dataArray.length / 16, dataArray.length);
                    highFreqPeakAmplitude = getPeakAmplitude(dataArray, 12 * dataArray.length / 16, dataArray.length);
                    midFreqAvgAmplitude = getAverageAmplitude(dataArray, dataArray.length / 16, 3 * dataArray.length / 16);
                    lowFreqAvgAmplitude = getAverageAmplitude(dataArray, 0, dataArray.length / 16);
                    lowFreqPeakAmplitude = getPeakAmplitude(dataArray, 0, dataArray.length / 16);
                }

                //smudge effect
                rotateSmudge = (rotateSmudge && !(lowFreqPeakAmplitude > ultimatePeak * 1.01) && midFreqAvgAmplitude + highFreqAvgAmplitude > avgAmplitude * 2)
                                || midFreqAvgAmplitude + highFreqAvgAmplitude > avgAmplitude * 3.2;
                splitSmudge = !growSmudge || ((splitSmudge && !(lowFreqPeakAmplitude < ultimatePeak * 0.8) && midFreqAvgAmplitude + highFreqAvgAmplitude < avgAmplitude * 3)
                                || midFreqAvgAmplitude + highFreqAvgAmplitude < avgAmplitude * 2);
                growSmudge = ((growSmudge) && highFreqPeakAmplitude + midFreqAvgAmplitude > highFreqAvgAmplitude + lowFreqAvgAmplitude / 2)
                                || (highFreqPeakAmplitude * 2 + midFreqAvgAmplitude > highFreqAvgAmplitude + lowFreqAvgAmplitude)
                                || (rotateSmudge && highFreqPeakAmplitude * 2 + midFreqAvgAmplitude > highFreqAvgAmplitude / 2 + lowFreqAvgAmplitude);
                if (rotateSmudge) {
                    if (backgroundRef.current.style.opacity !== "0.4") {
                        backgroundRef.current.style.opacity = "0.4";
                    }
                    rotationAngle = (rotationAngle + 1) % 360;
                    smudgeVelocity = (lowFreqAvgAmplitude / 10) * ((midFreqAvgAmplitude < highFreqAvgAmplitude * 5)? -1: 1);
                    // Save the current canvas state
                    smudgeCtx.save();
                    // Rotate the canvas
                    smudgeCtx.translate(smudgeCtx.canvas.width / 2, smudgeCtx.canvas.height / 2);
                    smudgeCtx.rotate(Math.PI / 180 * rotationAngle * smudgeVelocity);
                    smudgeCtx.translate(-smudgeCtx.canvas.width / 2, -smudgeCtx.canvas.height / 2);
                    // Draw the smudged image on the canvas
                    smudgeCtx.globalAlpha = 0.008;
                    smudgeCtx.drawImage(smudgeCtx.canvas, 0, 0);
                    // Restore the saved state
                    smudgeCtx.restore();
                } else if (splitSmudge) {
                    rotationAngle = 0;
                    if (highFreqPeakAmplitude * 1.8 <= avgAmplitude) {
                        if (backgroundRef.current.style.opacity !== "0") {
                            backgroundRef.current.style.opacity = "0";
                        }
                    } else {
                        if (backgroundRef.current.style.opacity !== "0.4") {
                            backgroundRef.current.style.opacity = "0.4";
                        }
                    }
                    imageData = smudgeCtx.getImageData(0, 0, smudgeCtx.canvas.width, smudgeCtx.canvas.height);
                    smudgeVelocity = (avgAmplitude < 10)? avgAmplitude * 2: (((smudgeVelocity < 0 && lowFreqPeakAmplitude >= ultimatePeak) || (lowFreqPeakAmplitude >= ultimatePeak && lowFreqAvgAmplitude * 0.5 > midFreqAvgAmplitude + highFreqAvgAmplitude))? -1: 1) * avgAmplitude / 4;
                    smudgeCtx.clearRect(0, 0, smudgeCtx.canvas.width, smudgeCtx.canvas.height);
                    smudgeCtx.putImageData(imageData, 0, -smudgeVelocity, 0, 0, smudgeCtx.canvas.width, smudgeCtx.canvas.height / 2);
                    smudgeCtx.putImageData(imageData, 0, smudgeVelocity, 0, smudgeCtx.canvas.height / 2, smudgeCtx.canvas.width, smudgeCtx.canvas.height / 2);
                }
                if (growSmudge) {
                    smudgeGrowVelocity = (((!rotateSmudge && smudgeGrowVelocity < 0 && lowFreqPeakAmplitude > midFreqAvgAmplitude + highFreqPeakAmplitude) || lowFreqPeakAmplitude > (midFreqAvgAmplitude + highFreqAvgAmplitude) * 2.8)? -1: 1) * (highFreqPeakAmplitude - highFreqAvgAmplitude * (rotateSmudge? 16: 4)) / (avgAmplitude * 20);
                    let widthOffset = (smudgeCtx.canvas.width * smudgeGrowVelocity);
                    let heightOffset = (smudgeCtx.canvas.height * smudgeGrowVelocity);
                    imageData = smudgeCtx.getImageData(0, 0, smudgeCtx.canvas.width, smudgeCtx.canvas.height);
                    hiddenCtx.clearRect(0, 0, hiddenCtx.canvas.width, hiddenCtx.canvas.height);
                    hiddenCtx.drawImage(smudgeCtx.canvas, 0, 0);
                    smudgeCtx.clearRect(0, 0, smudgeCtx.canvas.width, smudgeCtx.canvas.height);
                    smudgeCtx.drawImage(hiddenCtx.canvas, 0 - widthOffset / 2, 0 - heightOffset / 2, smudgeCtx.canvas.width + widthOffset, smudgeCtx.canvas.height + heightOffset);
                } else {
                    smudgeGrowVelocity = 0;
                }

                if (avgAmplitude > 25) {
                    if ((doublesGoal === 2 && midFreqAvgAmplitude > avgAmplitude * 2) || midFreqAvgAmplitude > avgAmplitude * 3.2) {
                        doublesGoal = 2;
                    } else {
                        doublesGoal = 1;
                    }
                }
                if (doubles !== doublesGoal) {
                    doubles += (doublesGoal - doubles) / 4.0;
                }

                convertToASCII(smudgeCtx, frequencyCtx, doubles);
            }

            const asciiArtChars = [' ', "'", '^', '*', ';', 'D', 'a', 'v', 'i', 'd', 'M', 'a', 's', 'h', '$', '%', '#', '@', '?', '0', '1', '!'];
            function convertToASCII(fromContext, toContext, doubles) {
                toContext.clearRect(0, 0, toContext.canvas.width, toContext.canvas.height);
                // Set the font for the ASCII art
                toContext.font = '28px monospace';
                
                // Get the image data from the original canvas
                var imageData = fromContext.getImageData(0, 0, fromContext.canvas.width, fromContext.canvas.height);
                var pixels = imageData.data;
                var char, r, g, b, alpha;
                var pixelGap = Math.floor(((fromContext.canvas.width * fromContext.canvas.height) / 8500));
                // Convert the image to black and white
                for (let i = 0; i < pixels.length; i += pixelGap * 4) {
                    r = pixels[i];
                    g = pixels[i + 1];
                    b = pixels[i + 2];
                    alpha = pixels[i + 3];
                    char = asciiArtChars[Math.floor(asciiArtChars.length * (Math.max(alpha, 1) / 256))];
                    // Fill the ASCII character with the original color
                    toContext.fillStyle = 'rgba(' + r + ',' + g + ',' + b + ',' + (alpha / 10.0) + ')';
                    toContext.fillText(char, (i / 4 / doubles) % toContext.canvas.width, Math.floor((i / 4) / toContext.canvas.width));
                }
            }
        
            function getAverageAmplitude(data, startIndex, endIndex) {
                startIndex = Math.trunc(startIndex);
                endIndex = Math.min(Math.trunc(endIndex), data.length);
                let sum = 0;
                for (let i = startIndex; i < endIndex; i++) {
                    sum += data[i];
                }
                return sum / (endIndex - startIndex - 1);
            }
        
            function getPeakAmplitude(data, startIndex, endIndex) {
                startIndex = Math.trunc(startIndex);
                endIndex = Math.min(Math.trunc(endIndex), data.length);
                let peak = 0;
                for (let i = startIndex; i < endIndex; i++) {
                    peak = Math.max(peak, data[i]);
                }
                return peak;
            }
        
            function drawVisualizationBar(index, barWidth, x, verticalCenter) {
                if (dataArray[index] > ultimatePeak) {
                    ultimatePeak = dataArray[index];
                }
                //take the square of the data like this will amplify peaks
                const adjustedData = 8 * dataArray[index] * Math.pow(dataArray[index] / ultimatePeak, 1.5 * (1 - (index / dataArray.length))) * ((index + (dataArray.length / 2)) / dataArray.length);
                let barHeight = adjustedData;
                let offsetX = (Math.random() - 0.5) * adjustedData / 32;
                let offsetY = (Math.random() - 0.5) * adjustedData / 32;
                let offsetWidth = (Math.random() - 0.5) * adjustedData / 32;
                smudgeCtx.fillRect(x + offsetX, verticalCenter - (barHeight / 4) + offsetY, barWidth + offsetWidth, barHeight / 2);
            }
        
            let lowFreqShiftingColorNumber = 0;
            function getLowFrequencyColor(dataArray) {
                let lowFrequencySum = 0;
                let lowFrequencyMax = 0;
                for (let i = 0; i < dataArray.length / 2; i++) {
                    lowFrequencySum += dataArray[i];
                    if (dataArray[i] > lowFrequencyMax) {
                        lowFrequencyMax = dataArray[i];
                    }
                }
                let lowFrequencyAverage = lowFrequencySum / (dataArray.length / 2);
                if (lowFrequencyMax > lowFrequencyAverage * 10 || lowFrequencyAverage > ultimatePeak / 2) {
                    lowFreqShiftingColorNumber = (lowFreqShiftingColorNumber + (lowFrequencyMax / (lowFrequencyAverage * 20)) + (lowFrequencyAverage / ultimatePeak)) % 255;
                }
                return `hsl(${map(lowFrequencyAverage, lowFreqShiftingColorNumber, 255, lowFreqShiftingColorNumber / 4, 360)}, 100%, 50%,` + (0.01) + `)`;
            }
        
            let highFreqShiftingColorNumber = 0;
            function getHighFrequencyColor(dataArray) {
                let highFrequencySum = 0;
                let highFrequencyMax = 0;
                for (let i = dataArray.length / 2; i < dataArray.length; i++) {
                    highFrequencySum += dataArray[i];
                    if (dataArray[i] > highFrequencyMax) {
                        highFrequencyMax = dataArray[i];
                    }
                }
                let highFrequencyAverage = highFrequencySum / (dataArray.length/ 2);
                if (highFrequencyMax > highFrequencyAverage * 200 || highFrequencyAverage > ultimatePeak / 2) {
                    highFreqShiftingColorNumber = (highFreqShiftingColorNumber + (highFrequencyMax / (highFrequencyAverage * 200)) + (highFrequencyAverage / ultimatePeak)) % 255;
                }
                return `hsl(${map(highFrequencyAverage, highFreqShiftingColorNumber, 255, highFrequencyAverage / 4, 360)}, 100%, 50%,` + (0.01) + `)`;
            }
        
            function map(value, start1, stop1, start2, stop2) {
                return start2 + (stop2 - start2) * ((value - start1) / (stop1 - start1));
            }

            drawVisualization();
        }
    }, [analyser, source, audioCtx]);

    useEffect(() => {
        if (audioCtx && audioElement) {
            setAnalyser(audioCtx.createAnalyser());
            setSource(audioCtx.createMediaElementSource(audioElement));
        }
    }, [audioCtx, audioElement]);

    useEffect(() => {
        if (audioElement) {
            function updateScrubBar() {
                if(audioElement && scrubBarRef.current) {
                    let currentTime = audioElement.currentTime;
                    let duration = audioElement.duration;
                    let scrubBarValue = (currentTime/duration) * 100;
                    scrubBarRef.current.value = scrubBarValue;
                }
            }
            audioElement.addEventListener("timeupdate", updateScrubBar);
            audioElement.src = (fileInputRef.current && fileInputRef.current.files[0])? URL.createObjectURL(fileInputRef.current.files[0]): process.env.PUBLIC_URL + '/music/song.mp3';
            audioElement.addEventListener("loadeddata", () => {
                updateVolume();
                setLoaded(true);
            });
            audioElement.addEventListener("ended", () => {
                setPlay(false);
            });
            setAudioCtx(new (window.AudioContext || window.webkitAudioContext)());
        }
    }, [audioElement]);

    // Initialize audio context and elements when the file input changes
    function loadAudio() {
        if (fileInputRef.current && fileInputRef.current.files[0]) {
            let filePath = fileInputRef.current.files[0].name;
            var allowedExtensions = /(\.mp3|\.wav|\.mp4)$/i;
            if (!allowedExtensions.exec(filePath)) {
                alert('Invalid file type for audio visualizer. Try again with an MP3, MP4, or WAV file.');
                return;
            }
        }
        if (audioElement) {
            audioElement.pause();
            audioElement.currentTime = 0;
        }
        setAnalyser(null);
        setSource(null);
        setAudioCtx(null);
        if (audioElement != null) {
            audioElement.src = "!";
            audioElement.remove();
        }
        setAudioElement(new Audio());
    }

    function updateVolume() {
        if(audioElement && volumeBarRef.current) {
            let volumeBarValue = volumeBarRef.current.value;
            audioElement.volume = volumeBarValue / 100;
        }
    }

    function updatePlayback() {
        if(audioElement && scrubBarRef.current) {
            let scrubBarValue = scrubBarRef.current.value;
            let currentTime = (scrubBarValue / 100) * audioElement.duration;
            audioElement.currentTime = currentTime;
        } else {
            loadAudio();
        }
    }

    function togglePlay(given) {
        if (audioElement && audioCtx) {
            audioCtx.resume();
            if ((given === true) || audioElement.paused) {
                audioElement.play();
                setPlay(true);
            } else {
                audioElement.pause();
                setPlay(false);
            }
        } else {
            loadAudio();
        }
    }

    return (
        <VisibilitySensor onChange={setIsVisible}>
            <div style={props.style} className='canvas-area no-select' ref={canvasAreaRef}>
                <br />
                <br />
                <div className="main-header-container">
                    <h2 className={"main-header"} style={play?{opacity: 0, transition: "2s opacity"}:{opacity: 0.8, transition: "3s opacity"}}>David Mash</h2>
                </div>
                <div className="control-panel" onClick={togglePlay}>
                    {play?
                    <img alt={""} src={process.env.PUBLIC_URL + '/images/pause-button.png'} className={"pause-button"} />
                    :
                    <img alt={""} src={process.env.PUBLIC_URL + '/images/play-button.png'} className={"play-button"} />
                    }
                </div>
                <br />
                <canvas ref={frequencyRef} id="frequency" onClick={togglePlay}/>
                <canvas ref={backgroundRef} id="background" />
                <img
                    alt={""}
                    src={process.env.PUBLIC_URL + '/images/main_images/' + props.imageNumber + '.jpeg'}
                    style={{objectFit: "cover", minHeight: "100%", height: "100%", minWidth: "100%", width: "auto", position: "absolute", zIndex: "-3", top: "0px", left: "0px"}}
                />
                <div
                    className='bottom-controls-1'
                    onMouseOver={() => {if (!mouseOverBottom1) setMouseOverBottom1(true);}}
                    onMouseLeave={() => {if (mouseOverBottom1) setMouseOverBottom1(false);}}
                    style={mouseOverBottom1?{opacity: 0.8, transition: "opacity 300ms"}:play?{opacity: 0, transition: "opacity 300ms"}:{opacity: 0.6, transition: "opacity 300ms"}}
                >
                    <input
                        className={"file-input song-upload-button"}
                        type="file"
                        ref={fileInputRef}
                        onChange={loadAudio}
                    />
                </div>
                <div
                    className='bottom-controls-2'
                    onMouseOver={() => {if (!mouseOverBottom2) setMouseOverBottom2(true);}}
                    onMouseLeave={() => {if (mouseOverBottom2) setMouseOverBottom2(false);}}
                    style={mouseOverBottom2?{opacity: 0.8, transition: "opacity 300ms"}:play?{opacity: 0, transition: "opacity 300ms"}:{opacity: 0.6, transition: "opacity 300ms"}}
                >
                    <img className="slider-icon-left" src={process.env.PUBLIC_URL + '/images/volume-down.png'} alt=""></img>
                    <input
                        ref={volumeBarRef}
                        className="volume-bar"
                        type="range"
                        min="0"
                        max="100"
                        id="volume-control"
                        defaultValue={50}
                        onInput={() => {updateVolume();}}
                    />
                    <img className="slider-icon-right" src={process.env.PUBLIC_URL + '/images/volume-up.png'} alt=""></img>
                </div>
                {loaded?
                <div
                    className='bottom-controls-3'
                    onMouseOver={() => {if (!mouseOverBottom3) setMouseOverBottom3(true);}}
                    onMouseLeave={() => {if (mouseOverBottom3) setMouseOverBottom3(false);}}
                    style={mouseOverBottom3?{opacity: 0.8, transition: "opacity 300ms"}:play?{opacity: 0, transition: "opacity 300ms"}:{opacity: 0.6, transition: "opacity 300ms"}}
                >
                    <input
                        ref={scrubBarRef}
                        className="scrub-bar"
                        type="range"
                        min="0"
                        max="100"
                        defaultValue={0}
                        id="scrub-bar"
                        disabled={!loaded}
                        onInput={() => {updatePlayback();}}
                    />
                </div>
                :
                <></>
                }
            </div>
        </VisibilitySensor>
    );
}

export default AudioVisualizer;
